import request from '@/utils/request'

/**
 * ID
 * @param taskId ID
 */
export function getTask(taskId) {
  return request({
    url: '/tasks/'+taskId+'/get/',
    method: 'get'
  })
}

/**
 * 
 * @param data id
 */
export function batchTask(data) {
  return request({
    url: '/tasks/batch/batch/',
    method: 'post',
    data
  })
}

/**
 * 
 * @param taskId ID
 */
export function progressTask(taskId) {
  return request({
    url: '/tasks/'+taskId+'/progress/',
    method: 'get'
  })
}
