import request from '@/utils/request'

/**
 * 
 * @param data 
 * @constructor
 */
export function ImageAdd(data) {
  return request({
    url: '/images/',
    method: 'post',
    headers: {
      'Content-Type': 'multipart/form-data'
    },
    data
  })
}

/**
 * 
 * @param id
 * @param data
 * @constructor
 */
export function ImageEdit(id,data) {
  return request({
    url: "/images/"+id+"/edit/",
    method: 'post',
    data
  })
}

/**
 * 
 * @param id id
 * @constructor
 */
export function ImageDelete(id) {
  return request({
    url: '/images/'+id+'/delete/'
  })
}

/**
 * 
 * @constructor
 */
export function ImageLocal() {
  return request({
    url: '/images/local/local/'
  })
}

/**
 * 
 * @param data 
 * @constructor
 */
export function ImageLocalAdd(data) {
  return request({
    url: '/images/local/local_add/',
    method: 'post',
    data
  })
}

/**
 * 
 * @param id
 * @constructor
 */
export function ImageDownload(id) {
  return request({
    url: '/images/'+id+'/download/'
  })
}

/**
 * 
 * @param id  ID
 * @constructor
 */
export function ImageShare(id) {
  return request({
    url: '/images/'+id+'/share/'
  })
}
